<template>
  <div>
    <el-table :data="list"
     row-key="id"
      border style="width: 100%">
      <el-table-column prop="id" label="用户编号" width="120"> </el-table-column>
      <el-table-column prop="username" label="用户名" width="140">
      </el-table-column>
      <el-table-column prop="roleid" label="所属角色" width="300"> </el-table-column>
      <el-table-column prop="status" label="状态" width="100">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1">启用</el-button>
          <el-button type="danger" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="操作" width="*">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.uid)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.uid)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next" @current-change="cPage" :page-size="size" :total="count">
</el-pagination>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
import {requestManageDel} from '../../../util/request';
import { successAlert, warningAlert } from '../../../util/alert';
export default {
  computed:{
    ...mapGetters({
      count:'manage/count',
      list:'manage/list',
      // page:'manage/page',
      size:'manage/size',
    })
  },
  methods:{
    ...mapActions({
      requestList:'manage/manageList',
      requestCount:'manage/changeCountActions',
      requestPage:'manage/changePageActions',
    }),
    // 修改当前页码数
    cPage(page){
      // console.log(page);
      this.requestPage(page);
      this.requestList();
    },
    // 编辑
    edit(id){
      this.$emit('edit',id);
    },
    // 删除
    del(id){
       this.$confirm('确定要删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(()=>{
          // 发起删除请求
          requestManageDel({uid:id}).then(res=>{
              if(res.data.code == 200){
                successAlert('删除成功');
                this.requestCount();
                this.requestList();
              }else{
                warningAlert(res.data.msg);
              }
          })
        }).catch(error=>error)
    }

  },
  mounted(){
    this.requestCount();
    this.requestList();
  }
}
</script>

<style>

</style>



