<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show">
  <el-form :model="form">
     <el-form-item label="所属角色" :label-width="formLabelWidth">
      <el-select v-model="form.roleid">
        <el-option label="--请选择--" value="" readonly></el-option>
        <!-- 动态请求数据 -->
        <el-option v-for="item in list" :key="item.id" :label="item.rolename" :value="item.id"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="用户名" :label-width="formLabelWidth">
      <el-input v-model="form.username" autocomplete="off"></el-input>
    </el-form-item>
     <el-form-item label="密码" :label-width="formLabelWidth">
      <el-input type="password" v-model="form.password" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="菜单状态" :label-width="formLabelWidth">
      <el-switch  v-model="form.status" active-color="blue" inactive-color="grey" :active-value="1"
    :inactive-value="2">
</el-switch>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="add" v-if="info.isAdd==true">添加</el-button>
    <el-button type="primary" @click="update" v-else>修改</el-button>
  </div>
</el-dialog>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
import {requestManageAdd,requestManageOne,requestManageUpdate} from '../../../util/request';
import { successAlert, warningAlert } from '../../../util/alert';
export default {
  props:['info'],
  data(){
    return {
    formLabelWidth:'120px',
     form:{
        roleid:'',
        username:'',
        password:'',
        status:1
     }
    }
  },
  computed:{
    ...mapGetters({
      list:'role/list',
    })
  },
  methods:{
    ...mapActions({
      requestRoleList:'role/roleList',
      requestManageList:'manage/manageList',
      requestCount:'manage/changeCountActions',
    }),
    cancel(){
      this.form = {
        roleid:'',
        username:'',
        password:'',
        status:1
      }
      this.info.show = false;
    },
    // 添加数据
    add(){
      requestManageAdd(this.form).then(res=>{
        if(res.data.code == 200){
          successAlert('添加成功');
          this.cancel();
          this.requestManageList();
          // 更新列表同时更新总记录数
          this.requestCount();
        }else{
          warningAlert(res.data.msg);
        }
      })
    },
    // 获取每一条修改数据
    getDetail(id){
      // 发起请求
      requestManageOne({uid:id}).then(res=>{
        if(res.data.code == 200){
          this.form = res.data.list;
          this.form.password = '';
        }
      })
    },
    update(){
      // 判断密码是否为空
      // 发送请求
      requestManageUpdate(this.form).then(res=>{
          if(res.data.code  == 200){
            successAlert('修改成功');
            this.cancel();
            this.requestManageList();
          }else{
            warningAlert(res.data.msg);
          }
      })
    }
  },
  mounted(){
    this.requestRoleList();
  }
}
</script>

<style>

</style>



